import { RM } from '@speedlo/tools'
import React from 'react'
import { Container } from 'reactstrap'
import styled from 'styled-components/macro'

import { FShopOpeningHoursFragment } from '../../graph/generated'
import { formatTime } from '../../helpers/formats'
import { ShopStatusDisplay } from './ShopStatusDisplay'

interface IProps {
  branch: Maybe<FShopOpeningHoursFragment>
}

const StyledShopOpeningHours = styled(Container)`
  margin: 0.4rem 0 0 0.1rem;
  display: flex;

  & div.times,
  & div.status {
    padding-left: 2rem;
  }

  & div.icon {
    margin: -0.2rem 0 0 -1rem;
    position: absolute;
  }
`

export const ShopOpeningHours: React.FC<IProps> = ({ branch }) => {
  if (!branch) {
    return null
  }
  const openingHours = branch.businessHours.filter(RM.propEq('closed', false))
  return (
    <StyledShopOpeningHours fluid={true}>
      <div className="icon flaticon-clock" />
      {openingHours.length > 0 && (
        <div className="times">
          {openingHours.map((businessHour, idx) => (
            <div data-testid="openingHours" key={idx}>
              {`${formatTime(businessHour.openAt)} - ${formatTime(
                businessHour.closeAt,
              )}`}
            </div>
          ))}
        </div>
      )}
      <div className="status">
        {branch && (
          <ShopStatusDisplay
            status={branch}
            deliveryTime={branch.defaultDeliveryTime}
            pickupTime={branch.defaultPickupTime}
          />
        )}
      </div>
    </StyledShopOpeningHours>
  )
}
