import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components/macro'

import { BackgroundThemed } from '../../common/BackgroundThemed'

interface IProps {
  url: Maybe<string>
}

const LogoLink: React.FC<IProps> = ({ url, children }) =>
  url ? <a href={url}>{children}</a> : <Link to="/">{children}</Link>

const LogoContainerStyled = styled.div`
  height: 200px;
  width: 400px;
  flex: 0 0 400px;
  & .shop__header__logo {
    height: 100%;
  }
`

export const ShopLogo: React.FC<IProps> = props => (
  <LogoContainerStyled>
    <LogoLink {...props}>
      <BackgroundThemed
        className="shop__header__logo"
        imageName="storeLogo"
        size="cover"
      />
    </LogoLink>
  </LogoContainerStyled>
)
