import './PickupMap.css'

import { OverlayView } from '@react-google-maps/api'
import { useWhen } from '@speedlo/hooks'
import { idPropEq } from '@speedlo/tools'
import { observer, useLocalStore } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { MapBranchMarker } from '../../common/MapBranchMarker'
import { FPickupBranchFragment } from '../../graph/generated'
import { gpsToLatLng } from '../../helpers/gps'
import { useFoodMenuQuery } from '../foodMenu/hooks/useFoodMenuQuery'
import { useMapPanning } from './hooks/useMapPanning'
import { useOrderCreate } from './hooks/useOrderCreate'
import { usePickupMapQuery } from './hooks/usePickupMapQuery'
import { PickupMapBranch } from './PickupMapBranch'

interface IProps {
  onConfirm(): void
}

export const PickupMap = observer<IProps>(({ onConfirm }) => {
  const { business } = useRoot()

  const store = useLocalStore(() => ({
    activeBranchId: null as Maybe<ID>,
  }))

  // prefetch food menu for active branch
  useFoodMenuQuery(store.activeBranchId)

  const { data, loading } = usePickupMapQuery()

  // select business branchId by default
  useWhen(
    () => business.branchId !== null && store.activeBranchId === null,
    () => {
      store.activeBranchId = business.branchId
    },
  )

  const { createPickupOrder } = useOrderCreate(onConfirm)

  const renderBranchMarker = (branch: FPickupBranchFragment) => (
    <MapBranchMarker
      key={`${branch.id}-marker`}
      branch={branch}
      onClick={() => {
        store.activeBranchId = branch.id
      }}
    />
  )

  const renderBranchInfo = (branch: FPickupBranchFragment) => (
    <OverlayView
      key={branch.id}
      position={gpsToLatLng(branch.gps)}
      getPixelPositionOffset={width => ({
        // some measurement hickup, width is 0 initially, use average center
        x: width === 0 ? -131 : -(width / 2),
        y: 25,
      })}
      mapPaneName={OverlayView.OVERLAY_MOUSE_TARGET}
    >
      <PickupMapBranch
        branch={branch}
        onClose={() => {
          store.activeBranchId = null
        }}
        onConfirm={() => {
          createPickupOrder(branch.id)
        }}
      />
    </OverlayView>
  )

  const panToGps = useMapPanning()

  if (loading) {
    return null
  }

  if (business.isBrand && data.brand) {
    const branches = data.brand

    const activeBranch =
      store.activeBranchId !== null &&
      branches.find(idPropEq(store.activeBranchId))

    // const businessBranch = branches.find(idPropEq(business.branchId))

    if (activeBranch) {
      panToGps(activeBranch.gps)
    }

    return (
      <div data-testid="PickupMap">
        {branches && branches.map(renderBranchMarker)}
        {activeBranch && renderBranchInfo(activeBranch)}
      </div>
    )
  } else if (data.branch) {
    const branch = data.branch
    panToGps(branch.gps)
    return (
      <div data-testid="PickupMap">
        {renderBranchMarker(branch)}
        {renderBranchInfo(branch)}
      </div>
    )
  }

  return null
})
