import { useObserver } from 'mobx-react-lite'
import React, { createFactory } from 'react'

import { useRoot } from '../../app/useRoot'
import { BaseMap } from '../../common/BaseMap'
import { DeliveryTypeEnum } from '../../graph/generated'
import { DeliveryMap } from './DeliveryMap'
import { PickupMap } from './PickupMap'

interface IProps {
  onConfirm(): void
}

const mapByOrderMethod = {
  [DeliveryTypeEnum.Messenger]: createFactory<IProps>(DeliveryMap),
  [DeliveryTypeEnum.Pickup]: createFactory<IProps>(PickupMap),
}

export const OrderMap: React.FC<IProps> = ({ onConfirm }) => {
  const { order, map } = useRoot()
  return useObserver(() => (
    <BaseMap model={map}>
      {mapByOrderMethod[order.orderMethod]({ onConfirm })}
    </BaseMap>
  ))
}
